<template>
    <div>

        <el-button @click="dialogVisible = true" size="mini">选择商品</el-button>
        <el-dialog  top="10px"  :visible.sync="dialogVisible"  width="1500px"  title="选择商品" :modal-append-to-body="false">

            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">注意：这里只展示未关联其他营销活动的产品</p>
                </div>
                    <el-form size="mini" v-model="searchForm" class="search-form"  :inline="true">
                        <el-form-item  label="商品名称" label-width="100">
                            <el-input v-model="searchForm.goods_name"></el-input>
                        </el-form-item>
                        <el-form-item  label="商品编号" v-model="searchForm.goods_sn" label-width="100">
                            <el-input></el-input>
                        </el-form-item>
                        <el-form-item  label="商品分类" label-width="100">
                            <product-category :no-add="true" :p-cat-id="searchForm.p_cat_id" :cat-id="searchForm.cat_id" @selCat="selCat"></product-category>
                        </el-form-item>
                        <el-form-item  label="商品模型" label-width="100">
                            <select-type v-model="searchForm.type_id" :type-id="searchForm.type_id"></select-type>
                        </el-form-item>
                        <el-form-item label="扩展参数">
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_free_shipping">包邮</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_plus_vip" >会员</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_commission" >分佣</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_hot" >热销</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_new" >新品</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_recommend" >推荐 | </el-checkbox>
                        </el-form-item>
                        <el-form-item label="添加日期">
                            <date-between  v-model="searchForm.date"></date-between>
                        </el-form-item>
                        <el-form-item>
                            <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                        </el-form-item>

                    </el-form>
                    <el-table :data="datas.list" @selection-change="selectIds" style="width: 100%" size="small">
                        <el-table-column
                                v-if="multiple == true"
                                type="selection"
                                width="55">
                        </el-table-column>
                        <el-table-column width="60">
                            <template slot-scope="scope">
                                <el-button type="text" @click="SelectGoods(scope.row)" >选择</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column prop="goods_id" label="编码/ID" width="130">
                            <template slot-scope="scope">
                                <p>系统ID：<span>{{scope.row.goods_id}}</span></p>
                                <p>商品编码：<span>{{scope.row.goods_sn}}</span></p>
                            </template>
                        </el-table-column>
                        <el-table-column label="商品名称" >
                            <template slot-scope="scope">
                                <div class="ellipsis-02">
                                    {{scope.row.goods_name}}
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column label="缩略图" width="80">
                            <template slot-scope="scope">
                                <img width="60" :src="scope.row.photo"/>
                            </template>
                        </el-table-column>
                        <el-table-column label="商品价格" width="140">
                            <template slot-scope="scope">
                                <p>市场价：<span style="color: red">￥{{scope.row.market_price}}</span></p>
                                <p>本店价：<span style="color: red">￥{{scope.row.shop_price}}</span></p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="goods_sn" label="模型/分类" width="200">
                            <template slot-scope="scope">
                                <p>商品模型：<span>{{scope.row.type_id}}</span></p>
                                <p>商品分类：<span class="ellipsis-01">{{scope.row.p_cat_id}}-->{{scope.row.cat_id}}</span></p>
                            </template>
                        </el-table-column>
                        <el-table-column prop="goods_sn" label="扩展信息" width="150">
                            <template slot-scope="scope">
                                <el-row>
                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_free_shipping == 1" size="mini">包邮</el-tag>
                                        <el-tag v-if="scope.row.is_free_shipping == 0" type="info" size="mini">包邮</el-tag>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_plus_vip == 1" size="mini">会员</el-tag>
                                        <el-tag v-if="scope.row.is_plus_vip == 0" type="info" size="mini">会员</el-tag>
                                    </el-col>

                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_commission == 1" size="mini">分佣</el-tag>
                                        <el-tag v-if="scope.row.is_commission == 0" type="info" size="mini">分佣</el-tag>
                                    </el-col>
                                </el-row>
                                <el-row>
                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_hot == 1" size="mini">热销</el-tag>
                                        <el-tag v-if="scope.row.is_hot == 0" type="info" size="mini">热销</el-tag>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_new == 1" size="mini">新品</el-tag>
                                        <el-tag v-if="scope.row.is_new == 0" type="info" size="mini">新品</el-tag>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-tag v-if="scope.row.is_recommend == 1" size="mini">推荐</el-tag>
                                        <el-tag v-if="scope.row.is_recommend == 0" type="info" size="mini">推荐</el-tag>
                                    </el-col>
                                </el-row>
                            </template>
                        </el-table-column>


                        <el-table-column prop="goods_sn" label="上/下架" width="80">
                            <template slot-scope="scope">
                                <el-switch
                                        :value="scope.row.is_online"
                                        :inactive-value="0"
                                        :active-value="1"
                                        @change="setOnline(scope.row)"
                                ></el-switch>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="page">
                        <div class="page-tool">
                            <el-button  size="mini" type="primary" @click="onlines(1)">确认选择</el-button>
                        </div>
                        <div class="page-mian">
                            <el-pagination @current-change="getList" :page-size="datas.limit"
                                           layout="total, prev, pager, next" :total="datas.count">
                            </el-pagination>
                        </div>
                    </div>
                </div>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from "@/api";
    import DateBetween from "../../../components/DateBetween";
    import ProductCategory from "../components/ProductCategory";
    import SelectType from "../type/components/SelectType";
    export default {
        name: 'ProductSelectSpec',
        components: {SelectType, ProductCategory, DateBetween},
        props:{
            //是否多选
            multiple:{
                type: Boolean,
                default:false,
            },
            prom_id: {
                type: Number,
                default: 0,
            },
            prom_type: {
                type: Number,
                default: 0,
            },
            goods_id: {
                type: Number,
                default: 0,
            },
            is_edit: {
                type: Number,
                default: 0,
            },
        },
        data() {
            return {
                dialogVisible:false,
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: this.initSearch(),
                //批量操作
                goods_ids: [],
            }
        },
        created() {
            this.getList();
        },
        methods: {
            initSearch(){
                return {
                    date:[],
                    is_plus_vip:0,
                    is_commission:0,
                    is_free_shipping:0,
                    is_recommend:0,
                    is_new:0,
                    is_hot:0,
                    cat_id:0,
                    p_cat_id:0,
                    type_id:0,
                    is_online:0,
                    goods_name:'',
                    goods_sn:'',
                    prom_type: this.prom_type,
                    goods_id: this.goods_id,
                    is_edit: this.is_edit,
                }
            },

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.goods.goods/specIndex", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    prom_type: this.prom_type,
                    goods_id: this.goods_id,
                    is_edit: this.is_edit,
                    is_plus_vip:this.searchForm.is_plus_vip,
                    is_commission:this.searchForm.is_commission,
                    is_free_shipping:this.searchForm.is_free_shipping,
                    is_recommend:this.searchForm.is_recommend,
                    is_new:this.searchForm.is_new,
                    is_hot:this.searchForm.is_hot,
                    cat_id:this.searchForm.cat_id,
                    p_cat_id:this.searchForm.p_cat_id,
                    type_id:this.searchForm.type_id,
                    is_online: 2,//this.searchForm.is_online,
                    end_add_time:this.searchForm.date[1] ? this.searchForm.date[1] :  0,
                    bg_add_time:this.searchForm.date[0] ? this.searchForm.date[0] : 0,
                    goods_name:'',
                    goods_sn:'',
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            selCat(val){

                this.searchForm.cat_id = val.cat_id;
                this.searchForm.p_cat_id = val.p_cat_id;
            },

            setOnline(row) {
                this.loading = true;
                api.post("/shop/admin.goods.goods/online", {
                    appToken: this.$store.getters.appToken,
                    goods_id: row.goods_id,
                }).then(res => {
                    this.loading = false;
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            },

            selectIds(val) {
                let Ids = [];
                val.forEach(val => {
                    Ids.push(val.goods_id);
                });
                this.goods_ids = Ids;
            },

            SelectGoods(val){
                this.dialogVisible = false;
                this.$emit("selGoods",val);
            },

            onlines(online) {

            },
        }
    }
</script>

